<template>
  <div>
    <div class="content-top">
      <img v-lazy="leisureTimeTop" style="width: 101.4%;margin-left: -0.6%;"/>
    </div>
    <div class="content-middle">
      <img v-lazy="leisureTimeImg2" alt/>
      <img v-lazy="leisureTimeImg3" alt/>
      <img v-lazy="leisureTimeImg4" alt/>
      <img v-lazy="leisureTimeImg5" alt/>
    </div>
    <div class="footer">
      <router-link to="/orderConfirm/leisureTime">
        <img v-lazy="leisureTimeFooter" alt/>
      </router-link>
    </div>

    <CouponModal/>
  </div>
</template>
<script>
  import CouponModal from '@/components/CouponModal';

  export default {
    components: {
      CouponModal,
    },
    data() {
      return {
        leisureTimeTop: this.$getCdnUrl("leisureTime/leisureTimeTop.png"),
        leisureTimeImg2: this.$getCdnUrl("leisureTime/leisureTime2.png"),
        leisureTimeImg3: this.$getCdnUrl("leisureTime/leisureTime3.png"),
        leisureTimeImg4: this.$getCdnUrl("leisureTime/leisureTime4.png"),
        leisureTimeImg5: this.$getCdnUrl("leisureTime/leisureTime5.png"),

        leisureTimeFooter: this.$getCdnUrl("life/lifeFooter.png")
      };
    },
    beforeCreate() {
      document.title = '我爱休闲时光';
    },
    created() {
      // this.$wx.init('/leisureTime'); // 由CouponModal触发
    },
  };
</script>

<style scoped>
  .content-top img {
    width: 100%;
    height: auto;
  }

  .content-middle {
    padding-bottom: 60px;
    background-color: #f0f0f0;
  }

  .content-middle img {
    width: 93%;
    margin: 4px 3.5% 0;
  }

  .footer img {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
  }
</style>



